body {
    margin: 0;
    padding: 0;
    font-size: 14px;
    line-height: 18px;
    font-family: "Roboto", "Arial", sans-serif;
    font-style: normal;
  }
  
  img {
    max-width: 100%;
    height: auto;
    vertical-align: top;
  }
  
  .visually-hidden:not(:focus):not(:active) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }